      const text = {
        addMember: L('Send invitation'),
        addMemberImmediate: L('Create invitation'),
        removeMember: L('Remove Member'),
        default: L('Change')
      }

      return text[this.variant] || text.default
    }
  },
  methods: {
    close () {
      this.$refs.modal.unload()
    },
